<template>
    <div id='gymbranch' class='leakind'>
        <div class='top'>
          <span class='fundInfo' @click='goback()'>销售管理</span>
          <img src='../../assets/images/icon_nav_arrow.png'/>
          <span class='branch'>{{text}}</span>
        </div>
        <div class='sale'>ID：{{form.id}}</div>
        <el-form ref="form" :model="form" label-width="80px" :rules='rules'>
            <div class='info'>
                <div class='left'>
                  <el-form-item label="姓名：" class ='formNew'  prop='name' tabindex = "-1" :class="isActive === 1 ? 'border-active' : ''">
                      <el-input v-model.number="form.name" class='inputNew'  placeholder="请输入" @focus="inDiv(1)" @blur="outDiv(1)"></el-input>
                  </el-form-item>
                  <el-form-item label="性别：" class='formNew' prop="sex" tabindex = "-1" :class="isActive === 2 ? 'border-active' : ''">
                      <el-select v-model="form.sex" placeholder="请选择" clearable  @focus="inDiv(2)" @blur="outDiv(2)" >
                        <el-option
                        v-for="(item, index) in sexlist"
                        :key="index"
                        :label="item.name"
                        :value="item.code"
                        :class="{whiteColor: index % 2 !== 0 ? true : false}">
                        {{item.name}}
                        <img src='../../assets/images/icon_right.png'/>
                        </el-option>
                     </el-select>
                  </el-form-item>
                  <!-- <el-form-item label="时长（分钟）：" class='formNew' prop='time'>
                      <el-input v-model.number="form.time" class='inputNew' placeholder="请输入时长" :maxlength="5"></el-input>
                  </el-form-item> -->
                  <el-form-item label="联系方式：" class ='formNew' prop='contact' tabindex = "-1" :class="isActive === 3 ? 'border-active' : ''">
                     <el-input v-model="form.contact" class='inputNew'  placeholder="请输入" @focus="inDiv(3)" @blur="outDiv(3)"></el-input>
                  </el-form-item>
                  <el-form-item label="证件类型：" tabindex = "-1" :class="isActive === 4 ? 'border-active' : ''">
                    <el-select v-model="form.personalType" placeholder="请选择" clearable  @focus="inDiv(4)" @blur="outDiv(4)" ref='cardTypeInfoCode'>
                        <el-option
                        v-for="(item, index) in cardTypelist"
                        :key="index"
                        :label="item.name"
                        :value="item.code"
                        :class="{whiteColor: index % 2 !== 0 ? true : false}">
                        {{item.name}}
                        <img src='../../assets/images/icon_right.png'/>
                        </el-option>
                     </el-select>
                  </el-form-item>
                  <el-form-item label="证件号：" tabindex = "-1" :class="isActive === 5 ? 'border-active' : ''">
                    <el-input v-model.number="form.personalIdNo" class='inputNew' placeholder="请输入" @focus="inDiv(5)" @blur="outDiv(5)"></el-input>
                  </el-form-item>
                  <el-form-item label="生日：" tabindex = "-1" :class="isActive === 6 ? 'border-active' : ''">
                    <el-date-picker
                        v-model="form.birthday"
                        type="date"
                        :default-value="startTime"
                        placeholder="选择日期"
                        class='inputNew'
                        @focus="inDiv(6)" 
                        @blur="outDiv(6)"
                        >
                    </el-date-picker>
                  </el-form-item>
                  <el-form-item label="所属门店：" class ='formNew' prop='belongBranchOfficeId'  tabindex = "-1" :class="isActive === 7 ? 'border-active' : ''">
                    <el-select v-model="form.belongBranchOfficeId" placeholder="请选择" clearable  @focus="inDiv(7)" @blur="outDiv(7)" ref='cardTypeInfoCode'>
                        <el-option
                        v-for="(item, index) in branchlist"
                        :key="index"
                        :label="item.name"
                        :value="item.id"
                        :class="{whiteColor: index % 2 !== 0 ? true : false}"
                        >
                        {{item.name}}
                        <img src='../../assets/images/icon_right.png'/>
                        </el-option>
                     </el-select>
                  </el-form-item>
                  <el-form-item label="入职时间：" prop='entryTime'  tabindex = "-1" :class="isActive === 8 ? 'border-active' : 'formNew'" >
                    <el-date-picker
                        v-model="form.entryTime"
                        type="date"
                        placeholder="选择日期"
                        class='inputNew'
                        @focus="inDiv(8)" 
                        @blur="outDiv(8)"
                        >
                    </el-date-picker>
                  </el-form-item>
                </div>
            </div>
            <div class='btn'>
                <el-form-item>
                   <el-button type="primary" @click='goback()'>取消</el-button>
                   <el-button @click="submit('form')">确定</el-button>
                </el-form-item>
            </div>
        </el-form>
        <!-- <div class='btn'>
            <div class='submit' @click="submit('form')">提交</div>
        </div> -->
    </div>
</template>
<script>
import {getallbranch, addSaleInfo, getStartBranch} from '../../services/service.js'
import {isApiResponseOk, getSessionStorage} from '../../utils/com.js'
import { setTimeout } from 'timers';
export default {
  data () {
    var checkContact = (rule, value, callback) => {
      if (value) {
        let FormatReg = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/  // 手机格式
        let mainReg =  /^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/
        if (!FormatReg.test(value) && !mainReg.test(value)) {
          callback(new Error('请填写正确的联系方式'))
        } else {
          callback()
        }
      } else {
        callback(new Error('联系方式不能为空'))
      }
    }
    return {
      form: {
        name: '',
        sex: '',
        contact: '',
        personalType: '',
        personalIdNo: '',
        birthday: '',
        belongBranchOfficeId: '',
        entryTime: ''
      },
      deadline: false, // 充值有效期
      rules: {
        name: [{required: true, message: '姓名不能为空', trigger: 'blur'}],
        sex: [{required: true, message: '性别不能为空', trigger: 'change'}],
        contact: [{validator: checkContact, trigger: 'blur'}],
        belongBranchOfficeId: [{required: true, message: '所属门店不能为空', trigger: 'change'}],
        entryTime: [
          {required: true, message: '入职时间不能为空', trigger: 'blur'}
        ]
      },
      sexlist: [
        {name: '男', code: '0'},
        {name: '女', code: '1'}
      ],
      cardTypelist: [
        {name: '身份证', code: 'MainlandIDCard'},
        {name: '香港居民证', code: 'HongkongIDCard'},
        {name: '护照', code: 'Passport'},
        {name: '台胞证', code: 'TaiBaoCard'}
      ],
      modify: false,
      text: '销售信息',
      isActive: -1,
      startTime: new Date(1990,0,1).getTime(),
      branchlist: [] // 门店list
    }
  },
  created () {
    this.id = this.$route.query.id
    this.userCardCode = this.$route.query.code
    if (this.id) {
      this.form = getSessionStorage('detailInfo') // 从之前的页面跳转传过来的数据
      this.form.sex = String(this.form.sex)
      this.text = '修改销售信息'
    }
    // 获取所有分店
    this.initBranch()
    // 获取所有的会员卡种类类型
    // this.initTypeList()
    // 获取所有的教练即销售
  },
  watch: {
  },
  computed: {
  },
  methods: {
    // 增加私教课
    async submit (ruleForm) {
      let self = this
      this.$refs[ruleForm].validate((valid) => {
        if (valid) {
            if (self.form.birthday) {  // 为了number类型转换为Date类型，不然校验时会报错
              self.form.birthday = new Date(self.form.birthday).getTime()
            }
            if (self.form.entryTime) {  // 为了number类型转换为Date类型，不然校验时会报错
              self.form.entryTime = new Date(self.form.entryTime).getTime()
            }
            self.addSale() // 添加销售
          if (!this.modify) {
          } else {
          }
        }
      })
    },
    // 查询所属门店
    async initBranch () {
        const res = await getStartBranch()
        if (isApiResponseOk(res)) {
            this.branchlist = res.data
        }
    },
    // 添加销售
    async addSale () {
        const res = await addSaleInfo(this.form)
        if (isApiResponseOk(res)) {
            this.$message({
                showClose: true,
                message: '增加成功',
                type: 'success'
            })
            this.$router.push({name: 'salelist'})
        }
    },
    // 返回到私教课程页面
    goback () {
      this.$router.push({name: 'salelist'})
    },
    inDiv: function (data) {
      this.isActive = data
    },
    outDiv: function (data) {
      this.isActive = -1
    }
  }
}
</script>
<style lang="sass">
.leakind
    .sale
        border-bottom: 1px solid #EAECEF;
        background: #fff;
        height: .6rem;
        line-height: .6rem;
        font-family: BebasNeueCyrillic;
        font-size: .2rem;
        color: #C0C4CC;
        letter-spacing: 0;
        /* line-height: 20.16px; */
        padding-left: .28rem;
    .el-form
        height: 6.3rem;
        .search
          .el-form-item__content
            display: flex;
            align-items: center;
            .el-input
              width: 80%;
              .el-input__inner
                position: relative;
                text-align: left;
                &:after
                  content: '|';
                  position: absolute;
          .sea
            width: .72rem;
            text-align: center;
            position: relative;
            font-size: .16rem;
            color: #20C6BA;
            cursor: pointer;
            &:before
                content: '|';
                position: absolute;
                left: -0.1rem;
                color: #EAECEF;
        .per
          font-family: PingFangSC-Regular;
          font-size: .14rem;
          color: #C0C4CC;
          letter-spacing: 0;
          line-height: .14rem;
          margin-top: .08rem;
          margin-bottom: .16rem;
          display: flex;
          .noperson
            color: #F44A6C;
            margin-right: .2rem;
            img
              margin-right: .08rem;
          .regist
            span
              color: #20C6BA;
              cursor: pointer;
        .person
          margin-bottom: 0px !important;
    .btn
        display: flex;
        justify-content: center;
        display: inline-block;
        text-align: center;
        .submit
            background: #20C6BA;
            border-radius: .04rem;
            color: #fff;
            font-size: .14rem;
            width: 1.2rem;
            height: .44rem;
            line-height: .44rem;
            color: #fff;
            text-align: center;
            margin-top: .3rem;
            cursor: pointer;
    .top
        margin-bottom: .2rem;
        display: flex;
        align-items: center;
        .fundInfo
            font-size: .17rem;
            color: #8597A1;
            cursor: pointer;
        .branch
            font-size: .21rem;
            color: #263238;
        img
            width: .16rem;
            margin-left: .12rem;
            margin-right: .2rem;
    .text
        font-size: .14rem;
        color: #C0C4CC;
        text-align: right;
    .balance
        font-size: .24rem;
        color: #263238;
        text-align: right;
    .dead
        font-size: .20rem;
        margin-top: .05rem;
    .tips
        font-size: .12rem;
        margin-bottom: .2rem;
        color: #C0C4CC;
        text-align: right;
        margin-top: -0.1rem;
    .timePer
        font-size: .11rem;
        font-family: PingFangSC-Regular;
        color: #8597A1;
        float: right;
        margin-top: -.14rem;
        margin-bottom: 0.2rem;
.leakind
  .search
    position: relative;
    .searchList
      position: absolute;
      top: 0.55rem;
      left: 0;
      margin: 0;
      background: #fff;
      width: 100%;
      border: 1px solid #E9ECEF;
      border-top: 0;
      padding: 0;
      z-index: 11;
      max-height: 3rem;
      overflow-y: auto;
      li
        list-style: none；
      .searchItem
        line-height: 0.4rem;
        padding: 0 0.1rem;
        box-sizing: border-box;
        cursor: pointer;
        &:hover
          background: #E9ECEF;
</style>



